<template>
  <div class="ringPieCharts">
    <div class="header">风险监测情况</div>
    <div id="ringPieCharts" ref="ringPieCharts"></div>
  </div>
</template>

<script>
  import * as echarts from 'echarts';
  //这是绘制图表的参数定义
  let optionTemp = {
    color: ['#4389f9', '#00caff', '#16ebd9', '#8affa3'],
    // grid: {
    //   width: '70%',
    //   height: '70%'
    // },
    series: [{
      name: 'Access From',
      type: 'pie',
      center: ['50%','50%'],
      radius: ['45%', '70%'],
      avoidLabelOverlap: false,
      label: {
        position: 'outer',
        alignTo: 'edge',
        margin: '25%',
        formatter: '{name|{b}}\n  {data|{c} }',
        minMargin: 5,
        edgeDistance: 10,
        lineHeight: 15,
        color: '#16ebd9',
        rich: {
          name: {
            fontSize: 12,
            fontWeight: 'bold'
          },
          data: {
            fontSize: 10,
            fontWeight: 'bold',
            lineHeight: 20,
            color: '#fff',
            align: 'right'
          }
        }
      },
      
      emphasis: {
        label: {
          show: true,
          // color:1,//之要加上这一句就可以实现颜色跟随
          fontSize: '40',
          fontWeight: 'bold'
        }
      },
      labelLine: {
        length: 15,
        length2: '8%',
        maxSurfaceAngle: 50,
        lineStyle: {
          width: 2
        }
      },
      data: [
        {
          value: 13,
          name: '重大缺陷'
        },
        {
          value: 22,
          name: '较大缺陷'
        },
        {
          value: 33,
          name: '一般缺陷'
        },
        {
          value: 66,
          name: '轻微缺陷'
        }
      ].sort((a,b) => {return b.value - a.value})
    }]
  };

  export default {
    name: "ringPieCharts",
    props: {
      index: {
        type: Number,
        default: () => {
          return null
        }
      },
      xData: {
        /*** * xData:['一月',二月]*/
        type: Array,
        default: () => {
          return []
        }
      },
      seriesData: {
        /*** * seriesData:[{
          name: 'legend1',
          type: 'bar',
          data: [20,10,30,60,70],
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#1268f3" // 0% 处的颜色
              }, {
                offset: 0.6,
                color: "#08a4fa" // 60% 处的颜色
              }, {
                offset: 1,
                color: "#01ccfe" // 100% 处的颜色
              }], false)
            }
          },
        },]*/
        type: Array,
        default: () => {
          return []
        }
      },
      //  决定x轴和y轴显示(单位)
      _zb: {
        /** :['单位','']*/
        type: Array,
        default: () => {
          return ['单位', '']
        }
      },

    },
    watch: {

    },
    data() {
      return {
        option: optionTemp,
      }


    },
    mounted() {
      this.init();
    },
    methods: {
      dataSet() {
        this.option.yAxis[0].name = this._zb[0]
        this.option.xAxis[0].name = this._zb[1]
        this.option.xAxis[0].data = this.xData
        if (this.seriesData.length > 0) {
          this.option.legend.data = []
          this.seriesData.forEach(item => {
            this.option.legend.data.push(item.name)
          })
          this.option.series = this.seriesData
        }
      },
      init() {
        // this.dataSet()
        let mychart = echarts.init(this.$refs.ringPieCharts, );
        window.addEventListener("resize", mychart.resize);
        //将会绘制图表的参数加到图表中
        window.console.log(this.option)
        
        mychart.setOption(this.option);
        let $this = this;
        mychart.on('click', function(params) {
          $this.$emit('getData', params, $this.index)
          // window.console.log(params);
        });

      },
    }

  }
</script>

<style scoped lang="scss">
  .ringPieCharts {
    width: calc(27% - 30px);
    // padding: 0 15px;
    height: 100%;
    margin-left: 15px;
    // display: flex;
    // align-items: center;
    // flex-wrap: wrap;
    position: relative;
    // align-content: space-between;
    .header {
      width: 100%;
      // position: absolute;
      // top: 0;
      text-align: left;
      font-family: MicrosoftYaHei-Bold;
      font-size: 14px;
      color: #FFFFFF;
      font-weight: bold;
    }
    
    #ringPieCharts {
      width: 100%;
      height: calc(100% - 30px);
      /*width: 97%;*/
      /*height: 99%;*/
    }
  }
</style>
